import React, { Component } from "react";
import Prism from "prismjs";
import "../../pages/prism.css";

class CodeSection extends Component {
  componentDidMount = () => {
    Prism.highlightAll();
  };
  componentWillMount = () => {
    Prism.highlightAll();
    console.log(Prism);
  };
  render() {
    return (
      <pre>
        <code className="language-jsx">{this.props.children}</code>
      </pre>
    );
  }
}

export default CodeSection;
